
.AboveAll{
display: flex;
justify-content: center;
align-content: center;
}

.Main{
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: repeat(2,minmax(1fr,2fr));
}

.Title{ 
  display: grid;
  font-size: 6vw;
  grid-column: 1/-1;
}

.btn1{
  display: grid;  
  grid-column-start:1;
  grid-column-end: 2;
  font-size: 4vw;
}

.btn2{
  display: grid;
  grid-column-start:2;
  grid-column-end: 3;
  font-size: 4vw;
  background-color: red;
}

.MainDiv{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.container{
  display: grid;
  border-radius: 2px;
  border-style: solid;
  border-color: black;
  grid-template-columns:3vw repeat(2,12vw);
  grid-template-rows: 3vw repeat(3,10vw);
  width: 27vw;
  height: 36vw;
  background-color: cyan;
  padding: 5px;
  margin: 2vw;
}

.No{
  display: grid;
  border-radius: 100%;
  border-color:black;
  border-style: solid;
  align-self: center;
  justify-self: center;
  padding: 0.25vw;
}

.Name{
  display: grid;
  grid-column: 2/-1;
  justify-self: center;
  align-self: center;
  font-style: italic;
  font-size:3vw;
}

.Description{
  justify-self: center;
  align-self: center;
  display: grid;
  grid-column:1/-1;
  font-style: oblique;
   font-size:1.5vw; 
  
  /* font-size: calc(); */
}

.img{
  display: grid;
  grid-row:3/5;
  grid-column: 1/-1;
  width: 27vw;
}

@media only screen and (max-width:500px){
  .MainDiv{
    display: grid;
    justify-items: center;
    align-items: center;
  }
  .container{
    display: grid;
    border-radius: 2px;
    border-style: solid;
    border-color: black;
    grid-template-columns:20px repeat(2,115px);
    grid-template-rows: 50px repeat(3,80px);
    width: 250px;
    height: 300px;
    background-color: cyan;
    padding: 5px;
  }
  .No{
    font-size: 5vw;
    align-self: center;
    justify-self: center;
  }

  .Name{
    font-size: 5vw;
    align-self: center;
    justify-self: center;
  }
  
  .Description{
    font-size: 3vw;
    align-self: center;
    justify-self: center;
  }

  .img{
    width:55vw;
    align-self: center;
    justify-self: center;
  }
}
